.coinDetails{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 100vh;
  width: 100%;

}

.coinDetails__return {
  color: #D4D9E3;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  }

.coinDetails__return:hover svg{
  color: #4DD9E8;
  -webkit-transform: translateX(-10px);
      -ms-transform: translateX(-10px);
          transform: translateX(-10px);
}
.coinDetails__return:hover p {
  color: #4DD9E8;
}

.coinDetails__return p{
  margin: 0;
  padding:0;
  font-size: 12px;
  padding-left: 3px;

}

.coinDetails__return svg{
  color: #D4D9E3;
  height: 30px;
  width: 30px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
}


.coinDetails__view{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  min-height: 100vh;
  width: 100%;

}

.coinDetails__description {
  height: 100%;
  width: 45%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 40px;
  text-align: center;
  color: #D4D9E3;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.coinDetails__description p,h3{
  padding: 0;
  margin: 0;
  margin-top: 15px;
}

.coinDetails__description span {
  color: #D4D9E3;
  background: #0384BE;
  border-radius: 10px;
  padding: 5px 10px;
  margin-top: 5px;
  font-weight: 500;
}

.coinDetails__description img{
  height: 200px;
  -webkit-animation: spinLogo 5s linear infinite;
          animation: spinLogo 5s linear infinite;
}

.coinDetails__details {
  width: 55%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;  
  -webkit-box-sizing: border-box;  
          box-sizing: border-box;
  padding-right:40px;
}

.coinDetails__details__market{
  display: grid;
  grid-template-columns: -webkit-max-content max-content;
  grid-template-columns: max-content max-content;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width:100%;
}

.coinDetails__details__quotes{
  width: 100%;
  display: grid;
  grid-template-rows: -webkit-max-content max-content;
  grid-template-rows: max-content max-content;
  grid-row-gap: 20px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.coinDetails__details__price{
  width: 100%;
  color: #D4D9E3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.coinDetails__details__price h3,h4{
  margin: 0;
}
.coinDetails__details__price h4{
  margin-bottom: 5px;
  font-weight: 300;
  font-size: 20px;
}

.coinDetails__details__price h3{
  font-weight: 500;
  font-size: 30px;
}

.coinDetails__details__supply{
  display: grid;
  grid-template-columns: -webkit-max-content max-content;
  grid-template-columns: max-content max-content;
  grid-column-gap: 15px;
  color: #D4D9E3;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.coinDetails__details__supply h4, h3{
  margin: 0;
}

.coinDetails__details__supply h4{
  font-weight: 300;
  margin-bottom: 5px;
}



@-webkit-keyframes spinLogo{
  from{
    -webkit-transform: rotateY(0);
            transform: rotateY(0)
  }
  to{
    -webkit-transform: rotateY(-360deg);
            transform: rotateY(-360deg)
  }
}



@keyframes spinLogo{
  from{
    -webkit-transform: rotateY(0);
            transform: rotateY(0)
  }
  to{
    -webkit-transform: rotateY(-360deg);
            transform: rotateY(-360deg)
  }
}

@media(max-width: 1060px){

  .coinDetails__description {
    width: 70%;
    margin: 40px 0;
  }

  .coinDetails__details {
    width: 80%;
    padding:0;
  }

  .coinDetails__details__market{
    margin-bottom: 40px;
  }

  .coinDetails__view{
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .coinDetails__return {
    position: fixed;
    top:0;
    left:0;
    }
    
}

@media(max-width: 660px){
  
  .coinDetails__description {
    width: 90%;
  }

  .coinDetails__details__market{
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    grid-column-gap: 0;
    grid-row-gap: 40px;
  }

  .coinDetails__details__price {
    text-align: center;
  }

  .coinDetails__details__price h3 span{
    text-align: center;
    display:inline-block;
  }
}

@media(max-width: 480px){
  .coinDetails__description {
    width: 100%;
  }

  .coinDetails__details__supply {
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    grid-row-gap: 15px;
  }
}


