.pie-chart{
  width:100%;
  height: 100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}

.pie-chart strong {
  font-weight: 300;
  color:red;
}

.pie-chart__title{
  color: #D4D9E3;
  font-weight: 300;
  text-align: center;
}

.d3-tip {
  line-height: 1;
  font-weight: 300;
  padding: 8px;
  background: rgba(3, 37, 99, 0.7);
  color: #D4D9E3;
  border-radius: 2px;
  pointer-events: none;
}

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: rgba(3, 37, 99, 0.7);
  position: absolute;
  pointer-events: none;
}

/* Northward tooltips */
.d3-tip.n:after {
  content: "\25BC";
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
  text-align: center;
}

/* Eastward tooltips */
.d3-tip.e:after {
  content: "\25C0";
  margin: -4px 0 0 0;
  top: 50%;
  left: -8px;
}

/* Southward tooltips */
.d3-tip.s:after {
  content: "\25B2";
  margin: 0 0 1px 0;
  top: -8px;
  left: 0;
  text-align: center;
}

/* Westward tooltips */
.d3-tip.w:after {
  content: "\25B6";
  margin: -4px 0 0 -1px;
  top: 50%;
  left: 100%;
}


@media(max-width: 660px){

  .pie-chart{
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 10px;
  }
}


